import { Component, OnInit } from '@angular/core';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { ChatService } from 'src/app/services/chat.service';

@Component({
  selector: 'app-password-reset-email',
  templateUrl: './password-reset-email.page.html',
  styleUrls: ['./password-reset-email.page.scss'],
})
export class PasswordResetEmailPage implements OnInit {

  public email = '';
  public error = '';
  public process = false;
  public sendSuccess = false;

  constructor(
    public authUser : AuthUserService,
    public chatService : ChatService
  ) { }

  ngOnInit() {
  }

  send(){

    if(this.email.length == 0){
      return
    }

    this.process = true;
    this.authUser.sendEmailResetPassword(this.email).subscribe(response => {
      console.log(response);
      if(response == 'passwords.sent'){
        this.sendSuccess = true;
        this.error = '';
      }else if(response == 'passwords.user'){
        this.sendSuccess = false;
        this.error = 'No se encuentra registrado el email ingresado';
      }
      this.process = false;
    },
    error => {
      this.process = false;
    })


  }

  ngOnDestroy(){
    console.log('destroy')
    this.sendSuccess = false;
  }

}
